// src/components/LineChart.js
import React, { useState } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, TimeScale } from 'chart.js';
import 'chartjs-adapter-date-fns'; // Import the date adapter
import './linechart.css'; // Import the CSS file

// Register components
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, TimeScale);

const LineChart = () => {
  const [days, setDays] = useState(30);

  const generateData = (days) => {
    const data = [];
    const labels = [];
    const startDate = new Date();
    startDate.setDate(startDate.getDate() - days);

    for (let i = 0; i < days; i++) {
      const date = new Date(startDate);
      date.setDate(startDate.getDate() + i);
      labels.push(date.toISOString().split('T')[0]); // Format date as YYYY-MM-DD
      data.push(Math.floor(Math.random() * 100)); // Random sales data
    }
    return { labels, data };
  };

  const { labels, data } = generateData(days);

  const chartData = {
    labels, // X-axis labels
    datasets: [
      {
        label: 'Daily earn $CUAN',
        data, // Data points
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
        labels: {
          font: {
            size: 16,
          },
        },
      },
      tooltip: {
        callbacks: {
          label: function (context) {
            return `Sales: ${context.raw}`;
          },
        },
        bodyFont: {
          size: 16,
        },
        titleFont: {
          size: 18,
        },
      },
    },
    scales: {
      x: {
        type: 'time',
        time: {
          unit: 'day',
        },
        title: {
          display: true,
          text: 'Date',
          font: {
            size: 16,
          },
        },
        ticks: {
          font: {
            size: 14,
          },
        },
      },
      y: {
        title: {
          display: true,
          text: '$CUAN',
          font: {
            size: 16,
          },
        },
        ticks: {
          font: {
            size: 14,
          },
        },
      },
    },
  };

  return (
    <div className="chart-container">
      <div className="chart-wrapper">
        <div className="select-wrapper">
          <label htmlFor="timeRange">Select Time Range: </label>
          <select
            id="timeRange"
            value={days}
            onChange={(e) => setDays(parseInt(e.target.value, 10))}
          >
            <option value={7}>7 Days</option>
            <option value={14}>14 Days</option>
            <option value={21}>21 Days</option>
            <option value={30}>30 Days</option>
          </select>
        </div>
    
        <Line data={chartData} options={options} />
      
      </div>
    </div>
  );
};

export default LineChart;
